<template>
  <div class="add-form-wraper">
    <el-form :model="addParamMsg" :rules="rules" ref="ruleForm" label-width="160px" >
      <el-form-item label="选择区域" required>
        <el-input v-model="addParamMsg.regionName" size="small" class="add-input" disabled></el-input>
      </el-form-item>
      <el-form-item label="品类" prop="goodsCategory">
        <el-select class="add-input" v-model="addParamMsg.goodsCategory" filterable placeholder="选择品类" size="small" clearable>
          <el-option
            v-for="item in goodsCategoryOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
        <span>蔬菜、水果为农残检测；水产、肉类为其他检测</span>
      </el-form-item>
      <el-form-item label="检测时间" prop="checkTime">
        <el-date-picker
          class="add-input"
          size="small"
          v-model="addParamMsg.checkTime"
          type="datetime"
          format="yyyy-MM-dd HH:mm:ss"
          value-format="yyyy-MM-dd HH:mm:ss"
          placeholder="请选择检测时间">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="检测报告" required>
        <image-upload 
          ref="imageUpload" 
          @setImageList="setImageList"/>
        <span>建议上传宽度为750像素的JPG或PNG图片，大小不能超过1M</span>
      </el-form-item>
      <hr class="s-slip-tb">
      <el-form-item>
        <el-button type="primary" size="small" @click="doSaveHandel('ruleForm')" :disabled="isDisabled" :icon="isDisabled ? 'el-icon-loading' : null">保存</el-button>
        <el-button size="small" @click="doCancelHandel">取消</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  import * as utils from '@/utils/utils'
  import ImageUpload from '@/components/Common/File/ImageUpload'
  import requestUrl from '@/api/GoodsMgt'
  export default {
    data() {
      const checkTimeValidator = (rule, value, callback) => {
        if (!value) {
          return callback(new Error('检测时间不能为空'))
        }else {
          callback()
        }
      }
      const goodsCategoryValidator = (rule, value, callback) => {
        if (!value) {
          return callback(new Error('品类不能为空'))
        }else {
          callback()
        }
      }
      return {
        isDisabled: false,
        addParamMsg: {
          DIFFCITY: true,
          id: '',
          regionCode: '', // 区域ID
          regionName: '',
          goodsCategory: '',
          checkTime: '',
          reportImageUrl: ''
        },
        goodsCategoryOptions: [
          {
            label: '蔬菜',
            value: 1
          },
          {
            label: '水果',
            value: 2
          },
          {
            label: '水产',
            value: 3
          },
          {
            label: '肉类',
            value: 4
          }
        ],
        rules: {
          checkTime: [
            {required: true, validator: checkTimeValidator, trigger: 'blur'}
          ],
          goodsCategory: [
            {required: true, validator: goodsCategoryValidator, trigger: 'change'}
          ]
        },
        imageList: [
          {
            imgUrl: '',
            index: 0
          }
        ]
      }
    },
    components: {
      "image-upload": ImageUpload
    },
    mounted() {
      let id = this.$route.params.id
      this.getDetail(id)
    },
    methods: {
      getDetail(id) {
        utils.showLoading()
        this.$http.get(requestUrl.viewQualityCheckReport, {id}).then(res => {
          utils.clearLoading()
          if(res.code === 0) {
            if(res.data) {
              this.addParamMsg.id = res.data.id
              this.addParamMsg.regionCode = res.data.regionCode
              this.addParamMsg.regionName = res.data.regionName
              this.addParamMsg.goodsCategory = res.data.goodsCategory ? parseInt(res.data.goodsCategory) : '' 
              this.addParamMsg.checkTime = res.data.checkTime ? utils.dateFormat('yyyy-MM-dd HH:mm:ss', res.data.checkTime) : ''
              this.addParamMsg.reportImageUrl = res.data.reportImageUrl || ''
              if(res.data.reportImageUrl) {
                let reportImageList = JSON.parse(res.data.reportImageUrl).map((url, index) => {
                  return {
                    imgUrl: url,
                    index
                  }
                })
                this.imageList = reportImageList
              }else {
                this.imageList = [
                  {
                    imgUrl: '',
                    index: 0
                  }
                ]
              }
              this.initImageUpload()
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          utils.clearLoading()
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      initImageUpload() {
        this.$nextTick(() => {
          let params = {
            imageList: this.imageList
          }
          this.$refs.imageUpload.initHandel(params) 
        })
      },
      setImageList(result) {
        const reportImageUrlList = result.imageList && result.imageList.length ? result.imageList.map(item => {
          return item.imgUrl
        }) : ""
        this.addParamMsg.reportImageUrl = JSON.stringify(reportImageUrlList)
      },
      doSaveHandel(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            if(JSON.parse(this.addParamMsg.reportImageUrl).length === 0) {
              this.$message('请上传检测报告图片')
              return
            }
            this.isDisabled = true
            this.$http.post(requestUrl.saveQualityCheckReport, {...this.addParamMsg}).then(res => {
              setTimeout(() => {
                this.isDisabled = false
              }, 800)
              if(res.code === 0) {
                this.$message.success("保存成功")
                this.$router.push('/qe')
                return
              }
              throw new Error(res.msg)
            }).catch(e => {
              this.isDisabled = false
              this.$notify.error({
                title: '提示',
                message: e.message
              })
            })
          } else {
            return false;
          }
        })
      },
      doCancelHandel() {
        this.$router.push('/qe')
      }
    },
    beforeRouteLeave (to, from, next){
      to.meta.keepAlive = true;
      next();
    }
  }
</script>


